#appbar{
width: 100%;
margin-left: -1px;
}
#rayan{
cursor: pointer;
font-family: Arial, Helvetica, sans-serif;
font-size: 25px;
font-weight: bold;
}
#btn_nav{
color: black;
width: 7%;
font-size: 20px;
height: 40px;
margin-left: 2%;
margin-right: 2%;
}
#icon{
font-size: 25px;
left: 81%;
top: 25%;
cursor: pointer;
position: absolute;
display: none;
}
@media (max-width:1000px){
    #btn_nav{

        display: none;
       
    }
    #icon{
        display: block;
    }
}